import { Task } from "../lib/types";


function fetchIt(url) {
    return Task((rej, res) => {
        return fetch(url)
            .then(res)
            .catch(rej);
    })
}

function getUrlOpenWeather(cityName) {
    return `http://api.openweathermap.org/data/2.5/weather?q=${cityName}&appid=dfc681da09a98952250b96eb90d71cf1`;
}

function ui(){
    const goButton=document.querySelector(".find");
    const input=document.getElementById("city-name");
    const results=document.querySelector(".results");
    console.log(goButton)
    goButton.addEventListener('click',()=>{
        var cityName=input.value.trim();
        return fetchIt(getUrlOpenWeather(cityName)).fork(console.error,console.log);
    })
}


export function app() {
    ui();
}



